<template>
    <div class="mycar">
        <p class="title">管理我的車</p>
        <p class="sub-title">MY CAR</p>
        <div class="mycar-content">
            <div class="scroll-x" :class="{'full': memberSideMenu}">
                <el-table
                    :data="tableData"
                    style="width: 100%"
                    :key="Math.random()"
                    >
                    <el-table-column
                        label="項目"
                        min-width="480"
                        >
                        <div class="search-result-card">
                            <div class="card-img">
                                <img src="~@/assets/img/cartype/photo-1.png" alt="" srcset="">
                            </div>
                            <div class="card-info">
                                <p class="card-tag"><i class="el-icon-price-tag"/>二手車商</p>
                                <p class="card-title">Aston Martin Vantage</p>
                                <p class="card-txt">In a pickup market gone fancy, the Silverado sticks to its basic-truck recipe. The steering is accurate, and the Silverado handles more like a big car than a big truck.</p>
                                <div class="card-money">
                                    <img src="~@/assets/img/cartype/money-icon-w.png" alt="" srcset=""> 
                                    <p>149.8 萬</p>
                                </div>
                                <div class="card-collect">
                                    <div :class="{'card-collect-active': collect }" @click="collectCar"><i class="el-icon-star-off"/>325 人蒐藏</div>
                                    <span />
                                    <div><i class="el-icon-view"/>1368 人瀏覽</div>
                                </div>
                            </div>
                        </div>
                    </el-table-column>
                    <el-table-column
                        prop="name"
                        label="買家"
                        min-width="180"
                        align="center">
                        <div class="head" v-for=" item in 10 " :key="item"  @click="openUser = true"><HeadIcon :width='35' />王曉明</div>
                    </el-table-column>
                    <el-table-column
                        prop=""
                        label="管理"
                        width="180"
                        fixed="right"
                        >
                        <div class="manage">
                            <div class="edit btn"><i class="el-icon-edit" />編輯</div>
                            <div class="delete btn"><i class="el-icon-delete-solid" />刪除</div>
                            <div class="switch btn" :class="{'switch-close' : !open}" @click="openCar"><i class="el-icon-check" />起用</div>
                        </div>

                    </el-table-column>
                </el-table>
            </div>
        </div>
        <ul v-if="!openParity" class="mycar-content-mb">
            <li v-for="(item,idx) in tableData" :key="idx">
                <div class="search-result-card">
                    <div class="card-img">
                        <img src="~@/assets/img/cartype/photo-1.png" alt="" srcset="">
                    </div>
                    <div class="card-info">
                        <p class="card-tag"><i class="el-icon-price-tag"/>二手車商</p>
                        <p class="card-title">Aston Martin Vantage</p>
                        <p class="card-txt">In a pickup market gone fancy, the Silverado sticks to its basic-truck recipe. The steering is accurate, and the Silverado handles more like a big car than a big truck.</p>
                        <div class="card-money">
                            <img src="~@/assets/img/cartype/money-icon-w.png" alt="" srcset=""> 
                            <p>149.8 萬</p>
                        </div>
                        <div class="card-collect">
                            <div :class="{'card-collect-active': collect }" @click="collectCar"><i :class="{'el-icon-star-on': collect }" class="el-icon-star-off"/>325 人蒐藏</div>
                            <span />
                            <div><i class="el-icon-view"/>1368 人瀏覽</div>
                        </div>
                        <div class="buyer">
                            <p @click="openBuyer(idx)" class="label">買家列表<i :class="['el-icon-plus', { 'el-icon-minus' : tableData[idx].buyerlist }]"></i></p>
                            <div :class="['list',{'open':tableData[idx].buyerlist}]">
                                <div class="head" v-for=" item in 10 " :key="item"  @click="openUser = true"><HeadIcon :width='35' />王曉明</div>
                            </div>
                        </div>
                    </div>
                    <div class="manage">
                        <div class="edit btn"><i class="el-icon-edit" />編輯</div>
                        <div class="delete btn"><i class="el-icon-delete-solid" />刪除</div>
                        <div class="switch btn" :class="{'switch-close' : !open}" @click="openCar"><i class="el-icon-check" />起用</div>
                    </div>
                </div>
            </li>
        </ul>
        <div v-if="openUser" class="user-page">
            <i class="el-icon-close"  @click="openUser = false"/>
            <User />
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
import HeadIcon from '../../../layout/headIcon.vue'
import User from './userpage.vue'
export default {
    name:'mycar',
    components: {
        User,
        HeadIcon,
    },
    computed: {
        ...mapState({
            memberSideMenu: state => state.memberSideMenu,
        }),
    },
    data() {
        return {
            collect: false,
            value: '',
            open: false,
            openUser: false,
            tableData: [
                {
                    name: '王曉明',
                    head: '',
                    checked: true,
                    buyerlist: false,
                }, 
                {
                    name: '王曉明',
                    head: '',
                    checked: false,
                    buyerlist: false,
                },
                {
                    name: '王曉明',
                    head: '',
                    checked: false,
                    buyerlist: false,
                }
            ],
        }
    },
    methods: { 
        openCar() {
            this.open = !this.open
        },
        collectCar() {
            this.collect = !this.collect
        },
        openBuyer(idx){
            this.tableData[idx].buyerlist = !this.tableData[idx].buyerlist;
        }
    }
}
</script>

<style lang='scss' scoped>
.mycar {
    padding: 20px 0;
    margin: 0 auto;
    width: 95%;
    .title {
        color: #333333;
        font-size: 40px;
        font-weight: bold;
        text-align: center;
    }
    .sub-title {
        color: #ef6d38;
        font-size: 25px;
        text-align: center;
    }
    .mycar-select {
        margin: 50px 0 20px 0;
        position: absolute;
        right: 50px;
    }
    .mycar-content {
        padding: 120px 50px;
        position: relative;
        .head {
            text-align: center;
            margin-bottom: 10px;
            display: flex;
            justify-content: center;
            align-items: center;
            .head-icon {
                margin: 0;
                margin-right: 10px;
            }
        }
        .search-result-card{
            display: flex;
            padding: 30px 0;
            &:nth-last-child(2) {
                border-bottom:none
            }
            .card-img {
                position: relative;
                cursor: pointer;
                img {
                    max-width: 100%;
                    width: 300px;
                }
            }
            .card-info {
                margin-left: 20px;
                
                .card-tag {
                    color: #fff;
                    background: #3cb07b;
                    line-height: 40px;
                    border-radius: 50px;
                    text-align: center;
                    padding: 0 30px 0 45px;
                    position: relative;
                    font-weight: bold;
                    max-width: 90px;
                    i{
                        background: #148451;
                        border-radius: 50px;
                        line-height: 30px;
                        width: 30px;
                        transform: rotate(45deg);
                        position: absolute;
                        top: 5px;
                        left: 5px;
                    }
                }
                .card-title {
                    font-size: 22px;
                    font-weight: bold;
                    margin: 30px 0 15px 0;
                    text-decoration: underline;
                }
                .card-txt {
                    max-width: 400px;
                    line-height: 25px;
                }
                .card-money {
                    background: #ef6d38;
                    line-height: 50px;
                    color: #fff;
                    font-size: 24px;
                    display: flex;
                    margin: 30px 0;
                    max-width: 225px;
                    img {
                        background: #ca582a;
                        padding:10px;
                    }
                    p {
                        padding-left: 10px;
                    }
                }
                .card-collect {
                    display: flex;
                    div {
                        margin: 10px;
                        padding: 0 10px;
                        transition: all .3s;
                        cursor: pointer;
                        i {
                            margin-right: 10px;
                        }
                        &:hover {
                            color: #ef6d38;
                        }
                    }
                    .card-collect-active {
                        color: #ef6d38;
                    }
                    span {
                        width: 1px;
                        display: block;
                        background: #dddddd;
                    }
                }
            }
            @media screen and (max-width: 768px){  
                align-items: center;
                flex-direction: column;
                .card-info {
                    margin: 20px 0;
                    .card-title {
                        text-align: center;
                    }
                    .card-money {
                        margin: 30px auto;
                    }
                    .card-collect {
                        margin: auto;
                        right: auto;
                    }
                }
                .card-tag {
                        margin: auto;
                    }
            }
        }
        @media screen and (max-width: 768px){ 
            width: 100vw;
            overflow:auto;
            min-width: 250px;
            padding: 50px 0;
        }
    }
    .manage {
        .btn {
            padding: 10px 30px;
            color: #fff;
            border-radius: 5px;
            transition: all .3s;
            width: max-content;
            margin-bottom: 10px;
            cursor: pointer;
            i {
                margin-right: 10px;
            }
        }
        .edit {
            background: #3cb07b;
            &:hover {
                background: #30996a;
            }
        }
        .delete {
            background: #e04c4c;
            &:hover {
                background: #c73c3c;
            }
        }
        .switch {
            background: #ef6d38;
        }
        .switch-close {
            background: #949494;
        }
    }

    .scroll-x{
        max-width: calc(100vw - 575px);
        overflow-x: auto;
        &.full{
            max-width: none;
        }
    }
    .user-page {
        .el-icon-close {
            position: fixed;
            z-index: 101;
            top: 5%;
            right: calc(50% - 370px);
            color: #fff;
            font-size: 6vmin;
            cursor: pointer; 
            @media screen and (max-width: 768px){ 
                right: calc(50% - 4vmin);
                font-size: 8vmin;
                border: 2px solid #fff;
                border-radius:30px;
                padding: 5px;
                top: 90%;
            }
        }
    }
    
    @media screen and (max-width: 768px){ 
        width: auto;
        .mycar-content{
            display: none;
        }
        .scroll-x{
            overflow: visible;
            max-width: none;
        }
        
    }


    //----- mobile list -----
    .mycar-content-mb {
        display: none;
        margin: 30px auto;
        >li{
            padding: 0 10%;
            margin-bottom: 60px;
            &:not(:first-child){
                padding-top: 50px;
                border-top: 1px solid #ddd;
            }
        }
        .card-img{
            margin-bottom: 30px;
            img{
                display: block;
                width: 100%;
                margin: 0 auto;
            }
        }
        .card-collect {
            display: flex;
            font-size: 15px;
            div {
                margin: 10px;
                padding: 0 10px;
                transition: all .3s;
                cursor: pointer;
                i {
                    margin-right: .5em;
                    font-size: 20px;
                    display: inline;
                    vertical-align: middle;
                }
                &:first-child{
                    padding-left: 0;
                    margin-left: 0;
                }
            }
            .card-collect-active {
                color: #ef6d38;
            }
            span {
                width: 1px;
                display: block;
                background: #dddddd;
            }
        }
        .buyer{
            margin: 30px auto;
            .label{
                position: relative;
                content: attr(data-label);
                display: block;
                background: #555;
                color: #fff;
                padding: 8px 15px;
                border-top-left-radius: 5px;
                border-top-right-radius: 5px;
                i{
                    position: absolute;
                    right: 15px;
                    top: 50%;
                    transform: translateY(-50%);
                    cursor: pointer;
                }
            }
            .list{
                padding: 5%;
                background: #eee;
                height: 140px;
                overflow: hidden;
                border-bottom-left-radius: 5px;
                border-bottom-right-radius: 5px;
                &.open{
                    height: auto;
                }
                .head {
                    text-align: center;
                    margin-bottom: 20px;
                    display: flex;
                    align-items: center;
                    .head-icon {
                        margin: 0;
                        margin-right: 10px;
                    }
                }
            }
        }
        .manage{
            display: flex;
            justify-content: center;
            align-items: center;
            >div{
                cursor: pointer;
                text-align: center;
                padding: 10px;
                width: 30%;
                margin: 0 1.5%;
            }
        }
    }

    @media screen and (max-width: 768px){ 
        .mycar-content-mb{
            display: block;
        }
    }

}



</style>


